<template>
  <div>
    <div class="mt-3 row">
      <!--   -->
      <div class="interest-table col-4">
        <div class="table-responsive">
          <table class="table table-hover table-bordered">
            <thead>
              <tr>
                <th colspan="4"> </th>
              </tr>
            </thead>
            <tbody>
              <template v-if="regions.length > 0">
                <template v-for="(region, index) in regions">
                  <tr :key="region.no">
                    <td>
                      <div style="float : left">
                        {{region.sigungu}}
                      </div>
                      <div style="text-align : right">
                        <a href="#" @click.prevent="searchEnv(index)"> <i class="fa fa-search" aria-hidden="true"></i></a>
                        &nbsp;
                        &nbsp;
                        &nbsp;
                        <a href="#" @click.prevent="searchShop(index)"> <i class="fa fa-search" aria-hidden="true"></i></a>
                        &nbsp;
                        &nbsp;
                        &nbsp;
                        <a href="#" @click.prevent="removeRegion(index)"><i class="fa fa-trash" aria-hidden="true"></i></a>
                      </div>  
                    </td>
                  <!-- </tr>
                  <tr :key="region.no">
                    
                  </tr>
                  <tr :key="region.no">
                  </tr>
                  <tr :key="region.no">
                  -->
                  </tr> 
                </template>
              </template>
              <template v-else>
                <tr>
                  <td>  .</td>
                </tr>
              </template>
            </tbody>
          </table>
        </div>
        <!--   -->
        <div id="adddiv">
          <router-link class="btn btn-primary" to="/interest/add"> </router-link>
        </div>
      </div>

      <!-- ,  -->
      <div class="information-table mt-3 col-8">
        <div id="map"></div>
        <div class="container">
          <div class="table-responsive info-table">
            <table class="table table-hover table-bordered">
              <template v-if="envs.length > 0">
                <thead>
                  <tr>
                    <th> </th>
                    <th>   </th>
                    <th></th>
                    <th></th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="env in envs" v-bind:key="env.no">
                    <td>{{env.name}}</td>
                    <td>{{env.bizcode}}</td>
                    <td>{{env.address}}</td>
                    <td>{{env.lng}}</td>
                    <td>{{env.lat}}</td>
                  </tr>
                </tbody>
              </template>
              <template v-if="shops.length > 0">
                <thead>
                  <tr>
                    <th @click="sortShop('name')"> </th>
                    <th @click="sortShop('type4')"> </th>
                    <th @click="sortShop('address')"></th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(shop, index) in shops" v-bind:key="shop.no" @click="clickShop(index)">
                    <td :title="shop.name">{{shop.name}}</td>
                    <td :title="shop.type4">{{shop.type4}}</td>
                    <td :title="shop.address">{{shop.address}}</td>
                  </tr>
                </tbody>
              </template>
            </table>
          </div>
        </div>
        <div class="container">
          <canvas id="chart"></canvas>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import http from "@/http-common.js";
import Chart from "chart.js";
import $ from 'jquery';

export default {
  data() {
    return {
      regions: [],
      shops: [],
      envs: [],

      curIdx: -1,

      mapList: [],
      map: {},
      mapCreated: false,
      mapCreate: 0
    };
  },
  watch: {
    mapCreate() {
      console.log("init map in mapCreate watcher called!");
      this.initMap();
      this.mapCreated = true;
    }
  },
  computed: {
    id() {
      return this.$store.state.id;
    }
  },
  created() {
    this.getRegionList();
  },
  // Mounted :   API   
  mounted() {
    if (window.kakao && window.kakao.maps) {
      if (!this.mapCreated && this.mapList.length > 0) {
        console.log("initMap in mounted is Called!");
        this.initMap();
        this.mapCreated = true;
      }
    } else {
      /* global kakao */
      const script = document.createElement("script");
      script.onload = () => kakao.maps.load(this.initMap);
      script.src =
        "http://dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=c0153be2e7c8003d17fd11e5d2f1dcfe&libraries=services";
      document.head.appendChild(script);
    }
  },
  methods: {
    searchEnv(index) {
      console.log(index);
      /*
      this.dong = this.regions[index].dong;
      this.curNo = this.regions[index].no;
      http
        .post("/rest/env", {
          dong: this.dong
        })
        .then(response => {
          this.envs = response.data.list;

          if (response.data.hasPaging) {
            this.envPaging = response.data.paging;
          } else {
            this.envPaging = "";
          }
          this.shops = [];
          this.shopPaging = "";
        })
        .catch(error => {
          alert("Error: " + error);
        });
      */
    },
    searchShop(index) {
      this.curIdx = index;

      http
        .post("/rest/shop", {
          sigudong: this.regions[index].sigungu
        })
        .then(response => {
          this.shops = response.data.list;
          this.envs = [];

          //  
          this.mapList = this.shops;
          this.mapCreate++;

          // chart 
          this.makeChart(response.data.chart)
        })
        .catch(error => {
          alert("Error: " + error);
        });
    },
    makeChart(chartData) {
      // ,  
      let labels = [];
      let data = [];
      for(let d of chartData) {
        labels.push(d.type);
        data.push(d.count);
      }

      let ctx = $("#chart");
      new Chart(ctx, {
        type: "radar",
        data: {
          labels: labels,
          datasets: [
            {
              data: data,
              label: " ",
              backgroundColor: "#ccebff",
              borderColor: "#0099ff",
              pointBackgroundColor: "#003d66"
            }
          ]
        }
      });
    },
    clickShop(index) {
      console.log(index);
    },
    sortShop(by) {
      http
        .post("/rest/shop/sort", {
          list: this.shops,
          by: by
        })
        .then(response => {
          this.shops = response.data;
        })
        .catch(error => {
          alert("Error: " + error);
        });
    },
    removeRegion(index) {
      if (index == this.curIdx) {
        this.shops = [];
        this.envs = [];
      }
      http
        .post("/rest/interest/delete", {
          sigungu: this.regions[index].sigungu,
          userid: this.regions[index].userid
        })
        .then(response => {
          if (response.data) {
            alert(" ");
          } else {
            alert("  ");
          }
          this.getRegionList();
        })
        .catch(error => {
          alert("Error: " + error);
        });
    },
    getRegionList() {
      http
        .get("/rest/interest/" + this.id)
        .then(response => {
          this.regions = response.data;
        })
        .catch(error => {
          alert("Error: " + error);
        });
    },
    initMap() {
      if(this.mapList.length == 0) return;

      //    
      var container = document.getElementById("map");
      //   
      var options = { level: 3 };
      if (this.mapList.length > 0) {
        options.center = new kakao.maps.LatLng(
          this.mapList[0].lat,
          this.mapList[0].lng
        );
      }

      //    .
      var map = new kakao.maps.Map(container, options);
      console.log("Add map on dom complete");

      //           
      var mapTypeControl = new kakao.maps.MapTypeControl();

      //     
      // kakao.maps.ControlPosition     TOPRIGHT   
      map.addControl(mapTypeControl, kakao.maps.ControlPosition.TOPRIGHT);

      //          
      var zoomControl = new kakao.maps.ZoomControl();
      map.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);
      console.log("Add control on map complete");

      this.map = map;

      let positions = [];

      //      LatLngBounds  
      //   Marker    
      var bounds = new kakao.maps.LatLngBounds();

      for (let shop of this.mapList) {
        let obj = {};
        obj.latlng = new kakao.maps.LatLng(shop.lat, shop.lng);
        obj.content = 
            '<div class="kakao-wrap">' + 
            '    <input id="kakao-no" type="hidden" value=' + shop.no + '/>' + 
            '    <div class="kakao-info">' + 
            '        <div class="kakao-title">' + 
                          shop.name + 
            '        </div>' + 
            '        <div class="kakao-body">' + 
            '            <div class="kakao-desc">' + 
            '                <div class="kakao-ellipsis">' + shop.address + '</div>' + 
            '            </div>' + 
            '        </div>' + 
            '    </div>' +    
            '</div>';
        obj.trickVal = shop.no;

        positions.push(obj);
        bounds.extend(obj.latlng);
      }

      //        
      map.setBounds(bounds);

      for (let i = 0; i < positions.length; i++) {
        //  
        let marker = new kakao.maps.Marker({
          map: map, //   
          position: positions[i].latlng //  
        });

        //    
        var infowindow = new kakao.maps.InfoWindow({
          content: positions[i].content //   
        });
        infowindow.trickVal = positions[i].trickVal;

        //  mouseover  mouseout  
        //     
        // for        
        kakao.maps.event.addListener(
          marker,
          "mouseover",
          this.makeOverListener(map, marker, infowindow)
        );
        kakao.maps.event.addListener(
          marker,
          "mouseout",
          this.makeOutListener(infowindow)
        );
        kakao.maps.event.addListener(
          marker,
          "click",
          this.makeClickListener(infowindow)
        );
      }
      console.log("Add all marker complete");
    },
    makeOverListener(map, marker, infowindow) {
      return function() {
        infowindow.open(map, marker);
      };
    },
    makeOutListener(infowindow) {
      return function() {
        infowindow.close();
      };
    },
    makeClickListener(infowindow) {
      return function() {
        console.log(infowindow.trickVal);
      };
    },
  }
};
</script>

<style scoped>
#adddiv {
  text-align: right;
}
#page {
  text-align: center;
}
.info-table {
  height: 500px;
}

th,
td {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

#map {
  height: 400px;
}
</style>